import cookies from "vue-cookies";
import imageUpload from "../public/imageUpload.vue";
import PasswordComfirm from "./passwordComfirm.vue";
import ImageEditPreview from "./imageEditPreview.vue";
export default {
  components: { imageUpload, PasswordComfirm, ImageEditPreview },
  data() {
    return {
      username: "",
      email: "",
      birthday: "",
      location: "",
      bio: "",
      icon: "",
      type_user: "user",
    };
  },
  computed: {
    user() {
      return this.$store.getters.defaultUser;
    },
  },
  methods: {
     back() {
       console.log('back')
       this.$emit('finish','menu')     
    },
    newImage(data) {
      this.icon = data;
      console.log(this.icon);
    },
    delete_res(data) {
      console.log(data);
      switch (data) {
        case true:
          this.icon = null;
          break;
        case false:
          alert("Delete falied");
          break;

        default:
          break;
      }
    },
    async updateAccount() {
      console.log(this.username);
      console.log(this.birthday);
      console.log(this.email);
      console.log(this.location);
      console.log(this.bio);
      console.log(this.icon);
      let password = prompt("Please input your password");
      await this.$axios({
        url: "/api/users",
        method: "patch",
        data: {
          token: cookies.get("token"),
          username: this.username,
          old_password: password,
          email: this.email,
          birthday: this.birthday,
          location: this.location,
          bio: this.bio,
          icon: this.icon,
        },
      }).then((res) => {
        console.log(res.data);
        cookies.set("user", res.data);
        alert("Edit Success");
        this.$emit("finish", "menu");
      });
    },
  },
  mounted() {
    if (this.user.username != "visiter") {
      this.username = this.user.username;
      this.birthday = this.user.birthday;
      this.email = this.user.email;
      this.location = this.user.location;
      this.bio = this.user.bio;
      this.icon = this.user.icon;
    }
  },
};
